@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0;border:0;background:none;box-sizing:border-box}body{background:url(/ToDo-App/static/media/bg-confetti.baa7805e.png);background-size:cover;color:#1a1a1a;font-family:"Poppins",sans-serif;font-weight:300}.main{display:flex;flex-direction:column;align-items:center;justify-content:center}.heading{font-size:4rem;font-weight:500;margin:5rem 0 1.5rem 0}.container{width:50%}#tinput,.add-btn{border:0;border-radius:8px;height:3.5rem;font-weight:300;font-family:"Poppins", sans-serif;transition:all 0.2s ease-in-out;outline:none}#tinput:focus,.add-btn:focus{box-shadow:0px 100px 287px rgba(0,0,0,0.0201946),0px 22.3363px 64.1052px rgba(0,0,0,0.0298054),0px 6.6501px 19.0858px rgba(0,0,0,0.05)}.search{display:flex;align-items:center;justify-content:space-evenly;margin:2rem 0}#tinput{color:#878787;background-color:#fafafa;padding:0 20px;flex-grow:4}#tinput:focus{color:#000}.add-btn{color:#000;padding:20px;cursor:pointer;margin-left:20px;background-color:rgba(75,189,121,0.5)}.add-btn:hover{color:#fff;background-color:#4bbd79;box-shadow:0px 100px 287px rgba(0,0,0,0.0201946),0px 22.3363px 64.1052px rgba(0,0,0,0.0298054),0px 6.6501px 19.0858px rgba(0,0,0,0.05)}.wrapper{margin-bottom:1rem;transition:all 0.5s ease-in-out}.wrapper:hover{box-shadow:0px 100px 287px rgba(0,0,0,0.0201946),0px 22.3363px 64.1052px rgba(0,0,0,0.0298054),0px 6.6501px 19.0858px rgba(0,0,0,0.05)}.fg{background-color:rgba(250,250,250,0.75);-webkit-filter:blur(2px);filter:blur(2px)}.task-box{display:flex;align-items:center;justify-content:space-between;height:3.5rem;background-color:rgba(250,250,250,0.5)}.task-txt{margin-left:20px;outline:none}.del-btn{cursor:pointer;padding:0 20px;color:#878787;height:inherit;outline:none}.message{height:3.5rem;display:flex;justify-content:center;align-items:center}@media screen and (min-width: 1170px){.lists{overflow:auto;max-height:500px}.lists::-webkit-scrollbar{width:5px}.lists::-webkit-scrollbar-track{background:#f1f1f1}.lists::-webkit-scrollbar-thumb{background:#888}.lists::-webkit-scrollbar-thumb:hover{background:#797979}}@media screen and (max-width: 1170px){.container{width:80%}.lists{overflow:auto;max-height:350px}.add-btn{font-size:small}}

